import {Component} from '@angular/core';

@Component ({
    selector:"app-nhanvien",
    template:`
            <div class = "nhanvien">
                <h3> 
                    ho ten: {{hoten}}                
                </h3>

                <h3>
                    nam sinh: {{2017- namsinh}}
                </h3>

                <h3>
                Game: {{sothich.game}} --
                Hoc: {{sothich.hoc}}
                </h3>

                <h4 >Mang : {{mang[1]}} -- {{mang.length}} </h4>
                <img src ="{{hinh}}"/>
                
            </div>
    `
})

export class NhanvienComponent{
hoten:string = "Tran van teo";
namsinh:number = 1995;
sothich = {game:"lmht", hoc:"toan"};
mang:string[] = ["one", "two"];
hinh:string = 'https://scontent.fsgn2-3.fna.fbcdn.net/v/t1.0-1/p160x160/14610911_1233700216692442_8023890972547532146_n.jpg?oh=65dcdd5ea2b1cf8435d5dd23d8a69fcb&oe=5A3F840C'
}


