import { Component } from "@angular/core";

@Component ({
    selector:"bai1",
    template:`
    
    <h1>{{so}}</h1>
    <input (click)="cong()" type="button" value ="+"/>
    <input (click)="tru()" type="button" value="-"/>
    <img (mouseover)="doihinh1($event)" 
    (mouseout)="doihinh2($event)" src="{{hinh}}"/>
    `

})

export class Bai1Component{
so:number = 0;
hinh:string = 'https://scontent.fsgn2-3.fna.fbcdn.net/v/t1.0-1/p160x160/14610911_1233700216692442_8023890972547532146_n.jpg?oh=65dcdd5ea2b1cf8435d5dd23d8a69fcb&oe=5A3F840C';


cong(){
    this.so++;
}
tru(){
    this.so--;
}
doihinh1(e){
    e.srcElement.src = 'https://scontent.fsgn2-3.fna.fbcdn.net/v/t1.0-1/p160x160/18194815_1048137925285999_420541598855055943_n.jpg?oh=a2eba6a5404366be73f2e01b80877b14&oe=5A7F8F14';
}
doihinh2(e){
    e.srcElement.src ='https://scontent.fsgn2-3.fna.fbcdn.net/v/t1.0-1/p160x160/14610911_1233700216692442_8023890972547532146_n.jpg?oh=65dcdd5ea2b1cf8435d5dd23d8a69fcb&oe=5A3F840C';
}
}


