import { Component } from "@angular/core";

@Component ({
    selector:"baihat",
    template:`<h3 [style.color]="'red'"> ten bai hat : {{tenbh}}</h3>
    <h3 [style.color]="mauchu"> ten bai hat : {{tenbh}}</h3>
    <div [class.aoxanh]="macao">hello</div>
    <img 
        (click)="clickHinh($event)"
        src ="{{hinh}}" 
     />
    `,
    styles:[
        '.aoxanh{ color: yellow; background-color: blue        }'

    ]

})

export class BaihatComponent{
tenbh:string = "mua";
mauchu:string = "blue";
macao:boolean = false;
hinh:string = 'https://scontent.fsgn2-3.fna.fbcdn.net/v/t1.0-1/p160x160/14610911_1233700216692442_8023890972547532146_n.jpg?oh=65dcdd5ea2b1cf8435d5dd23d8a69fcb&oe=5A3F840C'

    constructor(){
        this.tenbh = "phia sau 1 co gai";
    }

    clickHinh(e){
        console.log(e);
        alert (e.srcElement.src);
        e.srcElement.src = "https://scontent.fsgn2-3.fna.fbcdn.net/v/t1.0-1/p160x160/18194815_1048137925285999_420541598855055943_n.jpg?oh=a2eba6a5404366be73f2e01b80877b14&oe=5A7F8F14";
    }

}


